  --accent: 174.5 62.6% 49.6%;
  --accent-foreground: 0-0 10%;
  --destructive: 0 72.2% 50.6%;
  --destructive-foreground: 0 0 98%;
  --border: 0 0 89%;
  --input: 0 0 89%;
  --ring: 207.2 88.2% 57.8%;
  --brand-saffron: #FF9933;
  --card-background: rgba(242, 242, 242, 0.5);
  --glass-border: rgba(46, 46, 46, 0.1);
}
.logged-out-background {
  background-color: #0b0b0c;
  background-image: radial-gradient(1200px circle at top, #121214, #0b0b0c);
  background-attachment: fixed;
}
.logged-in-background {
  background-color: hsl(var(--background));
  background-image: radial-gradient(at 15% 15%, hsla(160, 100%, 50%, 0.3) 0px, transparent 50%),
    radial-gradient(at 85% 20%, hsla(330, 100%, 71%, 0.3) 0px, transparent 50%),
    radial-gradient(at 50% 85%, hsla(250, 100%, 70%, 0.3) 0px, transparent 50%),
    radial-gradient(at 5% 90%, hsla(42, 100%, 50%, 0.2) 0px, transparent 50%);
  background-attachment: fixed;
}
body {
  color: hsl(var(--foreground));
  font-family: 'Inter', sans-serif;
}
.simple body {
  background-image: none;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes float-watermark {
  0% {
    transform: translate(0, 0);
    opacity: 0.7;
    bottom: 2%;
    right: 2%;
  }
  20% {
    transform: translate(-45vw, -85vh);
    opacity: 0.4;
  }
  40% {
    transform: translate(-85vw, -40vh);
    opacity: 0.7;
  }
  60% {
    transform: translate(-40vw, 0);
    opacity: 0.4;
  }
  80% {
    transform: translate(0, -45vh);
    opacity: 0.7;
  }
  100% {
    transform: translate(0, 0);
    opacity: 0.7;
    bottom: 2%;
    right: 2%;
  }
}
@keyframes shadow-glow {
  0% {
    box-shadow: 0 0 5px var(--accent-cyan), 0 0 10px var(--accent-cyan);
  }
  50% {
    box-shadow: 0 0 20px var(--accent-cyan), 0 0 30px var(--accent-cyan);
  }
  100% {
    box-shadow: 0 0 5px var(--accent-cyan), 0 0 10px var(--accent-cyan);
  }
}
.shadow-glow {
  animation: shadow-glow 2s infinite ease-in-out;
}
.glass-card {
  background: var(--card-background);
  backdrop-filter: blur(20px) saturate(180%);
  border-radius: 1.5rem;
  border: 1px solid var(--glass-border);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.1);
}
.btn-glass {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 9999px;
  padding: 0.75rem 1.5rem;
  font-weight: bold;
  color: hsl(var(--foreground));
  transition: all 0.3s ease;
  box-shadow: 0 0 5px transparent, 0 0 10px transparent;
}
.btn-glass:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.2);
  transform: translateY(-1px);
}
.btn-glass-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: rgba(125, 95, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 9999px;
  width: 48px;
  height: 48px;
  color: hsl(var(--foreground));
  transition: all 0.3s ease;
}
.btn-glass-icon:hover {
  background: rgba(125, 95, 255, 0.2);
  box-shadow: 0 0 10px var(--brand-saffron), 0 0 5px var(--accent-green);
  transform: scale(1.1);
}
.input-glass {
  background: rgba(0, 0, 0, 0.2);
  border: 1px solid var(--glass-border);
  border-radius: 0.5rem;
  padding: 0.75rem 1.25rem;
  color: hsl(var(--foreground));
  transition: all 0.2s ease;
}
.input-glass:focus {
  outline: none;
  border-color: #a1a1aa;
  box-shadow: 0 0 10px rgba(161, 161, 170, 0.2);
}
.heptagon-clip {
  clip-path: polygon(50% 0%, 95% 25%, 95% 75%, 50% 100%, 5% 75%, 5% 25%);
}
