  font-family: 'Space Grotesk', sans-serif;
}
.font-body {
  font-family: 'Inter', sans-serif;
}
.font-cursive {
  font-family: 'Dancing Script', cursive;
}
.font-calligraphy {
  font-family: 'Great Vibes', cursive;
}
.font-italiano {
  font-family: 'Italianno', cursive;
